@tailwind base;
@tailwind components;
@tailwind utilities;

/* Modern Scrollbar for Devices Table */
.modern-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #a7c7e7 #232946;
}

.modern-scrollbar::-webkit-scrollbar {
  width: 8px;
  background: #232946;
}

.modern-scrollbar::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #a7c7e7 0%, #5f5aa2 100%);
  border-radius: 8px;
}

.modern-scrollbar::-webkit-scrollbar-track {
  background: #232946;
  border-radius: 8px;
}

/* --- Dark mode support --- */
:root {
  --primary-bg: #f4f4f9;
  --primary-text: #232946;
  --accent: #a7c7e7;
  --surface: #fff;
}

.dark {
  --primary-bg: #181a23;
  --primary-text: #f4f4f9;
  --accent: #5f5aa2;
  --surface: #232946;
}

html {
  background-color: #15161c;
}

body {
  background: linear-gradient(to bottom right, #1e2337, #232946, #15161c);
  background-attachment: fixed;
  min-height: 100vh;
  color: var(--primary-text);
  transition: background 0.3s, color 0.3s;
}

/* Accessibility: focus rings and high contrast */
:focus {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}

/* Toast notification transitions */
@keyframes toast-in {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }

  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.animate-toast-in {
  animation: toast-in 0.5s cubic-bezier(.4, 2, .3, 1) both;
}

/* Smooth theme transitions */
* {
  transition: background 0.3s, color 0.3s, border-color 0.3s;
}

/* Better contrast for buttons and cards */
button,
.card {
  background: var(--surface);
  color: var(--primary-text);
}

.dark button,
.dark .card {
  background: var(--surface);
  color: var(--primary-text);
}

/* Accessible hidden class */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}